@import url('https://fonts.googleapis.com/css?family=Roboto');
* {
  list-style: none;
}

.grid-2{
  display: grid;
  grid-template-columns: repeat(2,1fr);

}

body{
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-color:whitesmoke;
  color: #1b1b1b;

}
small{
  font-size: smaller;
  color: #797979;
}

h2,.white{
  color: rgb(0, 0, 0);
}

.round{
  border-radius: 50%;
  max-width: 7rem;
  max-height: 7rem;
}

a{
  color: #3468CC;
  /* color: #7A7C80; */
  text-decoration: none;
}

/* section 1 */
.section-1{
  padding-top: 10vh;
  text-align: center;
}

.section-1 p{
  font-size: 1.1rem;
  padding-bottom: 10px;
  margin:0;
}

.section-1 h2{
  font-size: 1.7rem;
  margin-bottom: 10px;
}

.section-1 a{
  font-size: 1.5rem;
  padding: 10px;
}
.section-1 li {
  text-align: left;
  margin-left: 2vw;
  margin-right: 2vw;
}
/* section 2 */
.section-2{
  padding-top: 13vh;
  width: 80%;
}

.section-2 h2{
  font-size: 1.7rem;
  
}

.section-2 p{
  font-size: 1.1rem;
  padding-bottom: 10px;
  margin:0;
}

.section-2 a{
  display: block;
  padding: 5px;
  font-size: 1.2rem;
  padding-left: 0;
  
}
