@media (max-width: 1200px) {
  #section-3 {
   -moz-column-count:    4;
   -webkit-column-count: 4;
   column-count:         4;
  }
}
@media (max-width: 1000px) {
  #section-3 {
   -moz-column-count:    3;
   -webkit-column-count: 3;
   column-count:         3;
  }
}
@media (max-width: 800px) {
  #section-3 {
   -moz-column-count:    2;
   -webkit-column-count: 2;
   column-count:         2;
  }
}
/* animations / utilities */
.section-2 a:hover{
  /* font-size: 1.3rem; */
  color: rgb(37, 37, 37);
  cursor: pointer;
  transition: 0.2s;
}

.section-1 a:hover{
  color: rgb(37, 37, 37);
  cursor: pointer;
  transition: 0.3s;
}

.white:hover{
  position: relative;
  padding-left: 10px;
}
.title-main{
  font-family: 'Carter One', cursive;
  color: black;
}

.bar {
  background: #c5c5c5;
  display: block;
  height: 0.,5px;
  border: 0,5px solid rgba(241, 241, 241, 0.3);
  border-radius: 3px;
  overflow: hidden;
  
}
.bar span {
height: 3px;
float: left;
background: #3468CC;
}

.html {
  width: 50%;
  animation: html 2s;
}
.css {
  width: 30%;
  animation: css 2s;
}
.angular {
  width: 40%;
  animation: angular 2s;
}
.ionic{
  width: 40%;
  animation: ionic 2s;
}
.design {
  width: 70%;
  animation: design 2s;
}
.cura {
  width: 85%;
  animation: cura 2s;
}
.print {
  width: 85%;
  animation: print 2s;
}

@keyframes html {
  0%{
      width: 0%;
  }
  100%{
      width:50%;
  }
}
@keyframes css {
  0%{
      width: 0%;
  }
  100%{
      width:30%;
  }
}
@keyframes angular {
  0%{
      width: 0%;
  }
  100%{
      width:40%;
  }
}
@keyframes ionic {
  0%{
      width: 0%;
  }
  100%{
      width:40%;
  }
}
@keyframes design {
  0%{
      width: 0%;
  }
  100%{
      width:70%;
  }   
}
@keyframes cura {
  0%{
      width: 0%;
  }
  100%{
      width:85%;
  }
}
@keyframes print {
  0%{
      width: 0%;
  }
  100%{
      width:85%;
  }
}
/* media queres */
@media(max-width:780px){
  .grid-2{
      grid-template-columns: 1fr;
  }
  .section-1{
      padding:0;
      padding-top: 5rem;
  }
  .section-2{
      padding: 0;
      padding-left: 1.5rem;
      padding-top: 2rem;
  }
}


